.source-holder,.download-holder,.QR-uploader{
  padding: 0 24px;
}
.download-holder{
  text-align: left;
}
.download-holder button{
  display: block;
  margin-top: 8px;
}
.canvas-holder{
  display: none;
  border-radius: 4px;
  border: 2px dashed #d9d9d9;;
  transform: scale(0.5, 0.5);
  transform-origin: left;
  transition: .5s;
}
.config-holder{
  margin-top: 8px;
}